                    <button type="button" :class="{active: order}" @click="changeOrder($event)">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                            <path d="M4 6H20M4 12H14M4 18H8" stroke="#050F28" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                            <path class="arrow" d="M14 16L18 20M18 20L22 16M18 20L18 4" stroke="#88A1DE" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                        </svg>
                        <span>Сначала новые</span>
                    </button>
                    <button type="button" :class="{active: !order}" @click="changeOrder($event)">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                            <path d="M4 6H20M4 12H14M4 18H8" stroke="#050F28" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                            <path class="arrow" d="M14 8L18 4M18 4L22 8M18 4L18 20" stroke="#88A1DE" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                        </svg>
                        <span>Сначала старые</span>
                    </button>
                </div>
            </div>
            <PostRow :posts="posts" ></PostRow>
            <button type="button" class="more" @click="loadMorePosts" v-if="page < totalPages">Показать больше</button>
        </div>
    </main>
    <footer class="wrapper">
        <div class="container">
            <span>Мой Дневничок</span>
            <button type="button" @click="scrollToTop">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                    <path d="M19 12L12 5L5 12M12 5L12 19" stroke="#050F28" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                </svg>
                <span>Наверх</span>
            </button>
        </div>
    </footer>
    <div id="loading" :class="{active : isQueryLoading}">
        <div class="loading__title"><span>Загрузка</span><div class="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div></div>
    </div>
    <Modal :showModal="showModal" @close="showModal = false" @createPost="fetchPosts"></Modal>
</template>

<script>
import Modal from '@/components/Modal';
import PostRow from '@/components/PostRow';
import axios from 'axios';

export default{
    components: {
        Modal, PostRow
    },
    data() {
        return {
            showModal: false,
            limit: 6,
            page: 1,
            order: true,
            totalPages: 0,
            posts: [],
            isQueryLoading: false,
        }
    },
    methods: {
        async fetchPosts(){
            try {
                this.page = 1;
                this.isQueryLoading = true;
                setTimeout(async () => {
                    const res = await axios.get(`http://localhost:8081/api/notes`,{
                        params: {
                            limit: this.limit,
                            page: this.page,
                            order: this.order,
                        }
                    });
                    this.posts = res.data;
                    this.isQueryLoading = false;
                }, 1000);
                
            } catch (error) {
                alert('error')
            } finally {
                
            }
        },
        async loadMorePosts(){
            try {
                this.page++;
                this.isQueryLoading = true;
                setTimeout(async () => {
                    const res = await axios.get(`http://localhost:8081/api/notes`,{
                        params: {
                            limit: this.limit,
                            page: this.page,
                            order: this.order,
                        }
                    });
                    this.posts = [...this.posts,...res.data];
                    this.isQueryLoading = false;
                }, 1000);
                
            } catch (error) {
                alert('error')
            }
        },
        async getPagesCount(){
            try {
                const res = await axios.get(`http://localhost:8081/api/notes/all`);
                this.totalPages = Math.ceil(res.data[0].count / this.limit);
            } catch (error) {
                alert('error');
            }
        },
        async changeOrder(event){
            if(event.target.classList.contains('active')){
                return;
            }
            this.order = !this.order;
            this.page = 1;
            this.fetchPosts();
        },
        async scrollToTop(){
            window.scrollTo({
                top: 0,
                behavior: "smooth"
            });
        }
    },
    mounted(){
        this.getPagesCount();
        this.fetchPosts();
    }
}
</script>

